main[data-layout-view^="fs-editor/"] {

  & .block :not(nav, .carousel) {

    & #aside-settings {

      & table {

        & > tbody {

          & tr {

            display: flex;
            flex-wrap: nowrap;
            flex-direction: row;
            align-items: center;
            justify-content: space-between;

            height: 36px;

            font-size: small;

            &.table-row--centered {
              justify-content: center;
            }

          }

        }

      }

    }

  }

}