nav > .common--breadcrumbs {

  font-weight: bold;

  &::before {
    display: flex;
    align-items: center;
    justify-content: center;

    font-family: var(--fa-solid--font-family);
    font-weight: var(--fa-solid--font-weight);
    content: var(--icon--back--content);
  }

  & .breadcrumb:not(:last-of-type)::after {
    display: inline-block;

    font-family: var(--fa-solid--font-family);
    font-weight: var(--fa-solid--font-weight);
    content: var(--icon--back--content);

    margin-left: var(--page--flex--gap);
  }

}